<template>
  <div v-if="searchResult">
    <!-- 搜索框 -->
    <div class="search">
      <van-icon
        name="arrow-left"
        size="24"
        color="#949494"
        @click="$router.back()"
      />
      <van-search
        show-action
        :value="$route.query.key"
        @click="$router.push({ path: '/search' })"
        autofocus
      >
        <template #action>
          <div>搜索</div>
        </template>
      </van-search>
    </div>
    <!-- 产品类型分类 -->
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

      <ClassesView :classes="searchResult.classes" />

      <!-- 搜索出来的产品 -->
      <template v-for="(item, index) in searchResult.list_v2">
        <SearchProduct
          v-if="item.view_type === 'view_search_product'"
          :result="item.body"
          :key="index"
        />
      </template>
    </van-pull-refresh>

      <div v-if="!searchResult?.list_v2?.length" class="noFound">
        <img src="@/assets/icon-search-empty.3d766c8b3f.png" alt="" />
        <p>没有找到商品，换个词搜搜吧</p>
      </div>

      <div v-if="!searchResult?.list_v2?.length" class="x">
        <img src="@/assets/title.png" alt="" />
        <CommodityBtmView :recomBtm="RecommendBlank" />
      </div>
  </div>
</template>

<script>
import CommodityBtmView from "@/components/search/SearchCommodityView.vue";
import ClassesView from "@/components/search/ClassesView.vue";
import SearchProduct from "@/components/search/SearchProduct.vue";
import { Toast } from 'vant';

export default {
  components: {
    SearchProduct,
    ClassesView,
    CommodityBtmView,
  },
  data() {
    return {
      searchResult: null,
      RecommendBlank: null,
      isLoading: false,
    };
  },
  watch: {
    searchResult() {
      if (this.searchResult?.list_v2?.length === 0) {
        this.getRecommendBlank();
      }
    },
  },
  methods: {
    // 下拉刷新
    onRefresh() {
      this.getSearchRes();

      setTimeout(() => {
        Toast('刷新成功');
        this.isLoading = false;
      }, 500);
    },
    
    // 搜索结果
    getSearchRes() {
      this.axios
        .get("hisearch/query_v3?query=" + this.$route.query.key)
        .then((res) => {
          // console.log("搜索结果", res.data.data);
          this.searchResult = res.data.data;
        });
    },
    // 查找推荐
    getRecommendBlank() {
      this.axios
        .get("https://apis.netstart.cn/xmsc/home/recommendBlank")
        .then((res) => {
          console.log("查找推荐", res.data.data);
          this.RecommendBlank = res.data.data.recom_list;
        });
    },
  },
  activated() {
    if (this.$route.query.key) {
      this.getSearchRes();
    }
  },
};
</script>

<style lang="less" scoped>
.search {
  display: flex;
  align-items: center;
  background: #f2f2f2;
  justify-content: space-around;
  padding: 0 8px;
  .van-search {
    background: #f2f2f2;
    padding: 12px;
    width: 94vw;
  }
}

.searchFound {
  h4 {
    text-align: left;
    padding: 0 18px;
  }
  .found {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    li {
      width: 40vw;
      margin: 8px 0;
      font-size: 14px;
    }
  }

  .suggest {
    text-align: left;
    li {
      margin: 8px 0;
      padding: 8px 18px;
      font-size: 14px;
    }
  }
}
.noFound {
  background: #f4f4f4;
  padding-top: 0.64rem;
  img {
    width: 2.34rem;
    height: 2.34rem;
    display: block;
    margin: 0 auto 0.24rem;
  }
  p {
    color: rgba(0, 0, 0, 0.54);
    font-size: 0.24rem;
    line-height: 0.28rem;
    text-align: center;
    padding-bottom: 0.48rem;
  }
}
.x {
  img {
    width: 7.2rem;
    height: 1.2rem;
  }
}
</style>